body, #app {
  overflow-x: hidden !important;
}

.custom-card {
  width: 150px;
  max-width: 200px;
  flex-grow: 1;
  margin: 10px;
}

.custom-card:active, .custom-card:hover {
  background-color: rgba(12, 108, 253, .72);
}

.clearfix {
  content: "";
  clear: both;
  display: table;
}

::-webkit-scrollbar {
  width: 8px;
  height: 10px;
  background: rgba(130, 180, 255, .56);
  border-radius: 8px;
}

::-webkit-scrollbar-thumb {
  background-color: #5084b9;
  border-radius: 8px;
}

#card {
  max-width: 400px;
  margin: 30px auto 0;
}



